import React from 'react';

import CitiesDescribePopup from '../popup/CitiesDescribePopup';
import data from './citiesdb';

function CitiesList() {
  const citiesItems = data.map(({ key, position, description }) => (
    <CitiesDescribePopup
      key={key}
      position={position}
      description={description}
    />
  ));
  return (
    <div style={{ display: 'none' }}>{citiesItems}</div>
  );
}

export default CitiesList;
