import React from 'react';

import ApartmentsSubject from './ApartmentsSubject';

import data from '../map-components/apartments2018db';

function ApartmentsList() {
  const apartmentsItems = data.map(({ key, position, description }) => (
    <ApartmentsSubject
      key={key}
      position={position}
      description={description}
    />
  ));
  return (
    <div style={{ display: 'none' }}>{apartmentsItems}</div>
  );
}

export default ApartmentsList;
